import React, { useEffect } from 'react'
import { TSettings } from '@/types'
import { ColorPicker, Form, Switch } from 'antd'

const OtherSettings: React.FC<{
	isLoading: boolean
	settings: TSettings
}> = ({ isLoading, settings }) => {
	const form = Form.useFormInstance()

	useEffect(() => {
		if (!isLoading) {
			form.setFieldsValue({
				btnColor: settings?.btnColor,
				showConfetti: settings?.showConfetti ?? true,
				showStock: settings?.showStock ?? true,
				showBuyerCount: settings?.showBuyerCount ?? true,
				enableVirtualList: settings?.enableVirtualList ?? false,
			})
		}
	}, [isLoading])
	return (
		<>
			<Form.Item name={['btnColor']} label="">
				<ColorPicker size="small" showText />
			</Form.Item>

			<Form.Item
				name={['showConfetti']}
				label=""
				valuePropName="checked"
			>
				<Switch size="small" />
			</Form.Item>

			<Form.Item
				name={['showStock']}
				label=""
				valuePropName="checked"
			>
				<Switch size="small" />
			</Form.Item>

			<Form.Item
				name={['showBuyerCount']}
				label=""
				valuePropName="checked"
			>
				<Switch size="small" />
			</Form.Item>

			<Form.Item
				name={['enableVirtualList']}
				label=""
				help=" Power Shop (ex 20) ()"
				valuePropName="checked"
			>
				<Switch size="small" />
			</Form.Item>
		</>
	)
}

export default OtherSettings
